body {
  margin: 0;
  padding: 0;
  
}

.snowflake {
  color: #fff;
  font-size: 1em;
  position: fixed;
  top: -2em;
}
img {
  width: 100%;
}

.wrapper {
  width: 50%;
  margin: 0;
  padding: 0 10px;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
.card {
  display: flex;
  width: 50%;
}
.span-text {
  display: flex;
  justify-content: center;
  margin-bottom: 5px;
  font-weight: bold;
}
.ant-input{
  margin-bottom: 10px;
  font-size: 16px;
}

.ant-card-hoverable{
  height: 240px;
  width: 400px;
}
.conteiner {
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
@media (max-width: 600px) {
  .wrapper {
    width: 100%;
    padding: 10px;
  }
  .secret-input {
    width: 70%;
  }
  .text-footer {
    margin-top: 5px;
    margin-left: 25px;
  }
}

.text-timer {
  display: flex;
  justify-content: center;
  color: blue;
 font-size: 30px;
}

@media (max-width: 600px) {
  .card {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
}

.ant-typography {
  font-size: 18px;
}

.conteiner-body {
  display: flex;
}

.footer {
  /* border: 2px solid red; */
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 100px;
}

.block-text {
  /* border: 2px solid red; */
display: flex;
align-items: center;
  width: 50%;
  height: 20%;
  padding: 20px;
}
.btn-block {
display: flex;
flex-direction: column;
justify-content: center;
  padding: 20px;

  width: 50%;
  height: 20%;

}

.ant-btn{
  width: max-content;
  font-size: 18px;
  padding: 0 10px 0 10px
}
.timer{
  width: 100%;
}